/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --darkBlue: hsl(203, 100%, 24%);
  --lightBlue: hsl(192, 100%, 38%);
  --darkGold: hsl(40, 76%, 50%);
  --lightGold: hsl(40, 69%, 64%);
  --aqua:	hsl(185, 92%, 35%);
  --black: hsl(0, 0%, 6%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-text: "Lato", Helvetica, Arial, sans-serif;
  /* body font */
  --font-heading: "Roboto", Helvetica, Arial, sans-serif;
  /* heading font */
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
  background-color: var(--white);
}

.container {
  width: 80%;
  max-width: 75rem;
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .container {
    width: 100%;
    position: relative;
  }
}

.row {
  padding: 0 0.625rem;
}

.flex {
  display: flex;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  max-width: 100%;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

/*--- HEADER START
-------------------------------------------------- */
/*--- HEADER END
-------------------------------------------------- */
h1,
h2,
h3 {
  margin-top: 0;
  font-family: var(--font-heading);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

@media (max-width: 39.9375em) {
  .header {
    height: 5.625rem;
  }
}
@media (min-width: 64em) {
  .header .hamburger {
    visibility: hidden;
  }
}
.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header .overlay {
  opacity: 0;
  top: 5.625rem;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--darkgreen80);
  z-index: 999;
}
@media (min-width: 40em) {
  .header .overlay {
    top: 6.875rem;
  }
}
@media (max-width: 39.9375em) {
  .header .overlay {
    position: absolute;
  }
}
.header__logo {
  height: 5.1875rem;
  margin: 0.9375rem 0;
  display: block;
}
@media (max-width: 39.9375em) {
  .header__logo img {
    width: 80%;
  }
}
.header__toggle {
  margin-right: 20px;
}
.header__toggle > span {
  display: block;
  width: 1.625rem;
  height: 0.125rem;
  background-color: var(--darkgreen);
  transition: all 300ms ease-in-out;
  transform-origin: 0.1875rem 0.0625rem;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 0.3125rem;
}

.nav {
  height: auto;
  position: relative;
}
.nav__link {
  color: var(--darkgreen);
  text-transform: uppercase;
  text-decoration: none;
  font-family: var(--font-heading);
  font-size: 1rem;
  font-weight: 500;
}
@media (max-width: 63.9375em) {
  .nav__link {
    margin-top: 1.25rem;
  }
}
.nav__link:hover, .nav__link:active {
  color: var(--blue);
}
@media (max-width: 39.9375em) {
  .nav__link {
    margin-top: 20px;
  }
}
.nav__item {
  margin: 0 0 0 1.5rem;
}
@media (max-width: 63.9375em) {
  .nav__item {
    padding: 1.25rem 0;
    margin: 0;
  }
}
.nav__list {
  display: flex;
  margin: 0;
  justify-content: flex-end;
  align-items: center;
  background-color: white;
  width: 100%;
  height: auto;
  text-align: center;
  top: 5.625rem;
  z-index: 999;
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .nav__list {
    display: none;
  }
}
.nav__list--open {
  position: absolute;
  display: block;
  background-color: white;
  width: calc(100% - 3rem);
  left: 50%;
  transform: translate(-50%);
  height: auto;
  text-align: center;
  top: 5.625rem;
  padding: 1.25rem 0 2.5rem 0;
  z-index: 999;
  margin: 0 auto;
  border-radius: 0 0 0.3125rem 0.3125rem;
}
@media (min-width: 40em) {
  .nav__list--open {
    top: 6.875rem;
  }
}

/* Footer
/* -------------------------------------*/
.footer {
  height: auto;
  background-color: var(--lightergreen);
  position: relative;
  display: flex;
  flex-direction: row;
}
.footer__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 2.5rem;
}
@media (max-width: 39.9375em) {
  .footer__wrapper {
    width: 100%;
  }
}
.footer__column {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
}
@media (max-width: 39.9375em) {
  .footer__column {
    flex-direction: column;
  }
}
@media (max-width: 39.9375em) {
  .footer__column--right {
    margin-top: 3rem;
  }
}
.footer__column--contact {
  display: flex;
  flex-direction: column;
}
.footer__column--contact .address {
  margin-bottom: 1.25rem;
}
.footer__column--contact .contact-prefix {
  color: var(--darkblue);
  font-weight: 700;
}
.footer__column--contact .digital a {
  color: var(--darkblue);
  font-weight: 700;
}
.footer__column--contact .digital a:hover {
  text-decoration: underline;
  cursor: pointer;
}
.footer__column--contact p {
  margin-top: 0;
  margin-bottom: 0;
}
.footer h3 {
  color: var(--darkblue);
  text-transform: uppercase;
  font-size: 2.375rem;
}
.footer .copyright {
  margin: 1rem auto;
}
@media (max-width: 63.9375em) {
  .footer .copyright {
    margin-top: 1rem;
    width: 90%;
    text-align: center;
  }
}
.footer .copyright p {
  font-size: 0.875rem;
}
.footer .copyright p a {
  color: var(--black);
  font-weight: 700;
}
.footer .copyright p a:hover {
  text-decoration: underline;
}